// export default EventDetailsPage;

"use client";
import { Button } from "@/components/ui/button";
import { CalendarDays, Locate, Ticket } from "lucide-react";
import Image from "next/image";
import { Card, CardContent } from "@/components/ui/Card";
import { eventU } from "@/constants/events";
import { useParams } from "next/navigation";

const EventDetailsPage = () => {
    const params = useParams();
    console.log(`params`, params)
    const eventId = params.eventId;
    // const eventId = String(params.id);

    // البحث عن الحدث المطلوب باستخدام الـ ID
    const event = event.find(e => e.id === eventId);
    console.log(`eventID` , eventId)

    if (!event) {
        return <div className="min-h-screen flex items-center justify-center">Event not found</div>;
    }

    return (
        <div className="min-h-screen bg-white">
            {/* القسم العلوي */}
            <section className="bg-gradient-to-r from-pink-500 to-purple-700 text-white py-10 px-6">
                <div className="max-w-7xl mx-auto flex flex-col lg:flex-row items-center">
                    <div className="w-full lg:w-1/2">
                        <Image
                            src={event.imageURL}
                            alt={event.title}
                            width={600}
                            height={400}
                            className="rounded-md"
                            priority
                        />
                        {event.isFree && (
                            <span className="inline-block mt-4 bg-purple-800 text-white text-xs px-3 py-1 rounded">
                                FREE
                            </span>
                        )}
                    </div>
                    <div className="w-full lg:w-1/2 mt-6 lg:mt-0 lg:pl-12">
                        <h1 className="text-3xl font-bold mb-4">{event.title}</h1>
                        <p className="mb-4 text-sm">{event.description}</p>
                        <ul className="space-y-2 mb-6 text-sm">
                            <li className="flex items-center">
                                <Locate className="w-4 h-4 mr-2" />
                                Location: {event.location}
                            </li>
                            <li className="flex items-center">
                                <CalendarDays className="w-4 h-4 mr-2" />
                                Date: {event.date}
                            </li>
                            <li className="flex items-center">
                                <Ticket className="w-4 h-4 mr-2" />
                                Entry: {event.isFree ? "Absolutely FREE for all!" : "Paid Entry"}
                            </li>
                        </ul>
                        <div className="flex gap-4">
                            <Button variant="default">Book Event</Button>
                            <Button className="bg-purple-700 hover:bg-purple-800">
                                Learn More
                            </Button>
                        </div>
                    </div>
                </div>
            </section>

            {/* تفاصيل الحدث */}
            <section className="px-6 py-12 max-w-4xl mx-auto">
                <h2 className="text-2xl font-bold mb-6">More Details</h2>
                <p className="mb-4 text-sm text-gray-700">{event.longDescription}</p>
                <ul className="list-disc list-inside text-sm text-gray-700 space-y-2">
                    {event.features?.map((feature, index) => (
                        <li key={index}>{feature}</li>
                    ))}
                </ul>
            </section>

            {/* أحداث ذات صلة */}
            {event.relatedEvents && event.relatedEvents.length > 0 && (
                <section className="bg-gray-50 py-12 px-6">
                    <h2 className="text-2xl font-bold text-center mb-8">Related Events</h2>
                    <div className="grid grid-cols-1 sm:grid-cols-2 md:grid-cols-3 gap-6 max-w-6xl mx-auto">
                        {event.relatedEvents.map((relatedEvent) => (
                            <Card key={relatedEvent.id}>
                                <CardContent className="p-4">
                                    <Image
                                        src={relatedEvent.imageURL}
                                        alt={relatedEvent.title}
                                        width={300}
                                        height={200}
                                        className="rounded-md"
                                    />
                                    <h3 className="text-lg font-semibold mt-4">{relatedEvent.title}</h3>
                                    <p className="text-sm text-gray-600">{relatedEvent.description}</p>
                                </CardContent>
                            </Card>
                        ))}
                    </div>
                </section>
            )}
        </div>
    );
};

export default EventDetailsPage;